"use client";
import React, { useState } from "react";
import { Table, Button, Form } from "react-bootstrap";

const UserTable = () => {
  const [users, setUsers] = useState([
    {
      id: 1,
      name: "John Doe",
      age: 25,
      email: "john@example.com",
      isEditing: false,
    },
    {
      id: 2,
      name: "Jane Doe",
      age: 30,
      email: "jane@example.com",
      isEditing: false,
    },
  ]);

  // Handle input change
  const handleInputChange = (id, field, value) => {
    setUsers((prevUsers) =>
      prevUsers.map((user) =>
        user.id === id ? { ...user, [field]: value } : user
      )
    );
  };

  // Toggle edit mode
  const toggleEdit = (id) => {
    setUsers((prevUsers) =>
      prevUsers.map((user) =>
        user.id === id ? { ...user, isEditing: !user.isEditing } : user
      )
    );
  };

  // Delete user
  const deleteUser = (id) => {
    setUsers((prevUsers) => prevUsers.filter((user) => user.id !== id));
  };

  // Create new user (duplicate last user for simplicity)
  const createUser = () => {
    const newUser = {
      id: users.length + 1,
      name: "",
      age: "",
      email: "",
      isEditing: true,
    };
    setUsers([...users, newUser]);
  };

  return (
    <div className="container mt-4">
      <h3>User Listing Table</h3>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Name</th>
            <th>Age</th>
            <th>Email</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user) => (
            <tr key={user.id}>
              <td>
                <Form.Control
                  type="text"
                  value={user.name}
                  disabled={!user.isEditing}
                  onChange={(e) =>
                    handleInputChange(user.id, "name", e.target.value)
                  }
                />
              </td>
              <td>
                <Form.Control
                  type="number"
                  value={user.age}
                  disabled={!user.isEditing}
                  onChange={(e) =>
                    handleInputChange(user.id, "age", e.target.value)
                  }
                />
              </td>
              <td>
                <Form.Control
                  type="email"
                  value={user.email}
                  disabled={!user.isEditing}
                  onChange={(e) =>
                    handleInputChange(user.id, "email", e.target.value)
                  }
                />
              </td>
              <td>
                {user.isEditing ? (
                  <Button
                    variant="success"
                    size="sm"
                    onClick={() => toggleEdit(user.id)}
                  >
                    Save
                  </Button>
                ) : (
                  <Button
                    variant="primary"
                    size="sm"
                    onClick={() => toggleEdit(user.id)}
                  >
                    Edit
                  </Button>
                )}
                <Button
                  variant="danger"
                  size="sm"
                  className="ms-2"
                  onClick={() => deleteUser(user.id)}
                >
                  Delete
                </Button>
                <Button
                  variant="info"
                  size="sm"
                  className="ms-2"
                  onClick={createUser}
                >
                  Create
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
};

export default UserTable;
